//BYU Colors

// blues
$navy: #002E5D;
$darknavy: #002040;
$medblue: #5D7998 ; // was 869DB5 but not quite accessible
$lightBlue: #e0f0cc;
$xlightblue: #C3DDF9;
$brightBlue: #0057B8;

$royal: #005eb8; // was #003DA5
$lightRoyal: #114BAD;

// There are 7-9 grays right now..
$xlightgray: #f2f2f2;
$lightgray: #e6e6e6; // will likely change to e6e6e6
$lightergray: #e6e6e6;
$medgray: #767676; // extra
$gray: #c5c5c5;
$darkgray: #666; // went from 4a4a4a to 666
$darkestgray: #141414;  // this should be used for body & default text.
// we are also using #666 and #777 in some places

// other colors
$red: #B3041A;

$yellow: #FCC015;
$lightyellow: #fef2d0;

$teal: #84DCB4;
$lightteal: #C4F7E0;

$green: #66B200; // doesn't work with accessibility
$lightgreen: #e0f0cc;

$white: #FFF;

/* ---- fonts ---- */
$gotham: "Gotham A", "Gotham B", Arial, sans-serif;
$vitesse: "Vitesse A", "Vitesse B", serif;


/* ------------- Headings Etc -------------------- */

h1, h2, h3, h4, .h1, .h2, .h3, .h4, .h8, .h9, .h10, .h11, .h12,
h1 a, h2 a, h3 a, h4 a, .h1 a, .h2 a, .h3 a, .h4 a, .h8 a, .h9 a, .h10 a, .h11 a, .h12 a {
  color: $navy;
}
h5 a, .h5 a,
h5, .h5, .h9.gray, .h9.gray a, .h11.gray, .h11.gray a {
  color: $medgray;
}
h6 a, .h6 a, .h7 a,.h8.white a, .h9.white a, .h11.white a, .h12.white a, .h13 a,
h6, .h6, .h7,.h8.white, .h9.white, .h10.white, .h10.white a, .h11.white, .h12.white, .h13, .h1-navy .h13  {
  color: #fff;
}
h1, h2, .h1, .h2 {
  font-family: $vitesse;
}
h3, h5, h6, .h3, .h5, .h6, .h7, .h8, .h9, .h10, .h11, .h12, .h13, .h1-navy .h13 {
  font-family: $gotham;
}

h1, .h1 {
  font-size: 35px;
  line-height: 34px;
  text-align: center;
}
h2, .h2, .larger-text {
  font-size: 25px;
  line-height: 34px;
  text-align: center;
}
h3, .h3 {
  font-size: 15px;
  letter-spacing: -.2px;
  line-height: 19px;
  a:hover, a:focus {
    font-weight: bold;
  }
}
h4 {
  font-size: 18px;
  line-height: 20px;
  a:hover, a:focus {
    color: $royal;
  }
}
h5 {
  font-weight: bold;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: .33px;
}
h6 {
  font-size: 25px;
  line-height: 27px;
  text-transform: uppercase;
  text-align: center;
}
.h7 {
  font-size: 14px;
  font-weight: bold;
  line-height: 14px;
  text-transform: uppercase;
  text-align: center;
}
.h8 {
  font-size: 14px;
  line-height: 16px;
  letter-spacing: -.2px;
  text-align: center;
}

.h9 {
  font-size: 16px;
  font-weight: 500;
  line-height: 40px;
  letter-spacing: .4px;
  a:hover, a:focus  {
    color: $royal;
  }
}
.h10 {
  font-size: 35px;
  line-height: 40px;
  font-weight: bold;
  &.white {
    color: #fff;
  }
}

// one case i couldn't add the class, so need a mixin
// no mobile adjustment
@mixin h11() {
  font-family: $gotham;
  font-size: 35px;
  font-weight: bold;
  line-height: 40px;
  @media screen and (max-width: 1024px){
    font-size: 26px;
  }

}
.h11 {
  @include h11();
}
.h12 {
  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  font-weight: 500;
  /*letter-spacing:*/
  a:hover, a:focus  {
    color: $royal;
  }
}
$h13-mobile-font:45px;
$h13-tabletp-font:90px;
$h13-tabletl-font: 120px;
$h13-desktop-font:135px;
$h13-desktopw-font: 200px;
.h13 {
  font-size: $h13-mobile-font;
  /*line-height:*/
}
.view-content-page-header .views-field-title {
  margin-top: ($h13-mobile-font *.73*-1);
  margin-bottom: ($h13-mobile-font *.73);
  h1 {
      background-color: $darknavy;
  }
}


@media screen and (min-width: 768px) {
  .h13 {
    font-size: $h13-tabletp-font;
  }
  .view-content-page-header .views-field-title {
    margin-top: ($h13-tabletp-font *.54*-1);
    margin-bottom: ($h13-tabletp-font *.50);
  }

}
@media screen and (min-width: 1024px) {
  h2, .h2, .larger-text {
    font-size: 30px;
  }
  h3, .h3 {
    font-size: 17px;
    line-height: 23px;
  }
  .h13 {
    font-size: $h13-tabletl-font;
  }
  .view-content-page-header .views-field-title {
    margin-top: ($h13-tabletl-font *.50*-1);
    margin-bottom: ($h13-tabletl-font *.50);
  }


}
@media screen and (min-width: 1224px) {
  h1, .h1 {
    font-size: 40px;
  }
  h2, .h2, .larger-text {
    font-size: 35px;
  }
  h3, .h3 {
    font-size: 20px;
    line-height: 26px;
  }
  /* we may not do this this way */
  .h8 {
    font-size: 16px;
    line-height: 17px;
  }
  .h9 {
    font-size: 20px;
    letter-spacing: .5px;
    line-height: 48px;
  }
  .h10 {
    font-size: 60px;
    line-height: 82px;
    letter-spacing: -2.5px;
    font-weight: 500;
  }
  .h13 {
    font-size: $h13-desktop-font;
  }
  .view-content-page-header .views-field-title {
    margin-top: ($h13-desktop-font *.48*-1);
    margin-bottom: ($h13-desktop-font *.50);
  }

}
@media screen and (min-width: 1824px) {

  .h13 {
    font-size: $h13-desktopw-font;
  }
  .view-content-page-header .views-field-title {
    margin-top: ($h13-desktopw-font *.44*-1);
    margin-bottom: ($h13-desktopw-font *.45);
  }
}


// general mixins

@mixin fieldWithMaxWidth() {
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
  // katria added these max-width adjustments for mobile & tablet - need to make sure this works for everyone's.
  // Aleni has told me specifics for some of these numbers and others are guesses based on the mockups.
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    max-width: 700px;
  }
  @media screen and (min-width: 1024px) and (max-width: 1223px) {
    max-width: 900px;
  }
  @media screen and (min-width: 1224px) and (max-width: 1423px) {
    max-width: 1024px;
  }
}

@mixin tabletAreaWithMaxWidth() {
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
    padding: 0px!important;
  }
}

@mixin navy-circle($circle-width) {
  background: $navy;
  padding: 25px ;
  border: 2px solid $medgray;
  border-radius: 50%;
  width: $circle-width;
  height: $circle-width;
  display: flex;
  flex-direction: column;
  /* flex-wrap: wrap; */
  align-items: center;
}




@mixin vertical-dots() {
  display: flex;
  //flex-wrap: wrap;
  flex-direction: column;
  margin: 15px 0px;
}
@mixin gray-dot() {
  color: $medgray;
  font-size: 60px;
  margin: 0px;
}

@mixin button() {
  padding: 12px 25px;
  a:hover, a:focus  {
    text-decoration: none;
  }
}

@mixin hover-to-royal() {
    color: white;
    text-decoration: none;
    background-color: $royal;
    transition: background 0.2s;
}
@mixin hover-to-navy() {
    color: white;
    text-decoration: none;
    background-color: $navy;
    transition: background 0.2s;
}
.hover-to-royal:focus,
.hover-to-royal:hover {
    @include hover-to-royal();
}
.hover-to-navy:focus,
.hover-to-navy:hover {
    @include hover-to-navy();
}

.royal-button a {
  @include button();
  background: $royal;
  color: #fff;
  text-decoration: none;
  &:hover, a:focus {
      @include hover-to-navy();
  }
}

.medblue-button a {
  @include button();
  background: $medblue; /* change to medium blue */
  color: #fff;
  &:focus,
  &:hover {
    @include hover-to-royal();
  }
}

// For A-Z Index, Colleges & Departments, etc
@mixin letterHeaders() {
  margin: 20px 0px 5px;
  font-weight: 500;
  @media screen and (max-width: 1024px) {
    margin: 20px 0px 0px;
  }
}